@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');

a{
  color: black !important;
}

.brand-logo {
  font-family: 'Grand Hotel', cursive;
  margin-left: 20px;
}

.home-card {
  max-width: 500px;
  height: max-content;
  margin: 26px auto;
}

.mycard{
  margin-top: 30px;
}

.auth-card{
  max-width: 450px;
  padding: 30px;
  text-align: center;
  margin: 10px auto;
}

.auth-header{
  font-family: 'Grand Hotel', cursive;
}

.input-field input[type=text]:focus {
  border-bottom: 1px solid #0454f4 !important;
  box-shadow: 0 1px 0 0 #0454f4 !important;
}

.input-field input[type=password]:focus {
  border-bottom: 1px solid #0454f4 !important;
  box-shadow: 0 1px 0 0 #0454f4 !important;
}

.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.item{
  width: 30%;
  margin-bottom: 10px;
}

.material-icons:hover{
  cursor: pointer;
}

.follow,.unfollow {
  margin: 10px;
}